@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap');

/* 1. GLOBAL FONTS, BOX-SIZING, NO HORIZONTAL SCROLL */
html, body {
    width: auto;  /* <-- CHANGE FROM 100vw TO auto */
    max-width: none;  /* <-- CHANGE FROM 100vw TO none */
    min-width: 0;
    overflow-x: hidden;
    box-sizing: border-box;
    font-family: 'Lato', 'Helvetica Neue', sans-serif;
    background-color: #F4F7FA;
}

*, *::before, *::after {
    font-family: inherit;
    box-sizing: inherit;
}

/* 2. ANT DESIGN LAYOUT, SIDEBAR, CONTENT */
.ant-layout,
.ant-layout-content,
.ant-layout-sider,
.ant-layout .ant-layout-content {
    width: 100%;
    max-width: 100vw;
    min-width: 0;
    box-sizing: border-box;
    overflow-x: hidden;
    background: transparent;
}

.ant-layout-sider {
    min-width: 220px !important;
    max-width: 260px !important;
    width: 240px !important;
    box-sizing: border-box;
}

.ant-layout-content {
    width: 100%;
    min-width: 0;
    box-sizing: border-box;
    background: none;
}

/* 3. OUTER DASHBOARD GRID LAYOUT (THE CRUCIAL FIX) */
.container {
    display: grid;
    grid-template-columns: minmax(220px, 260px) 80%; 
    width: 100vw;
    max-width: 100vw;
    min-width: 0;
    overflow-x: hidden;
    box-sizing: border-box;
    height: 100vh;
    justify-content: start; 
}

/* 4. SIDEBAR & CONTENT */
.sidebar {
    margin-top: 10px;
    margin-bottom: 10px;
    background-color: white;
    border-radius: 10px;
    border: 1px solid #D7D7D7;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-weight: bolder;
    min-width: 0;
    box-sizing: border-box;
}

.content {
    width: 100%;
    overflow-x: hidden;
    background-color: transparent;
    /* background-color: white; */
    padding: 0 10px;
    box-sizing: border-box;
    min-width: 0;
}

/* 5. SIDEBAR MENU */
.corelytics_sidebar {
    margin: 10px;
    background-color: white;
    padding: 10px 0;
    border-radius: 10px;
    border: 1px solid #D7D7D7;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-weight: bolder;
}

.corelytics_sidebar .ant-menu-item {
    margin-bottom: 20px;
}
.corelytics_sidebar .ant-menu-item:last-child {
    margin-bottom: 0;
}
.corelytics_sidebar img {
    margin-bottom: 20px;
}

.heading {
    font-size: 24px;
    font-weight: 700;
    color: #1a3353;
    margin: 0;
}
.titBar {
    background-color: #F9FAFB;
    padding: 16px 24px;
    border-bottom: 1px solid #D7D7D7;
}

/* Ant Design Menu overrides */
:global(.ant-menu) {
    background-color: #003D8F !important;
    color: white !important;
    font-weight: 500;
    border-inline-end: none !important;
}
:global(.ant-menu-item) {
    color: white !important;
    background-color: #003D8F !important;
}
:global(.ant-menu-item:hover) {
    background-color: #004cae !important;
}
:global(.ant-menu-submenu-title) {
    color: white !important;
    background-color: #003D8F !important;
}
:global(.ant-menu-submenu-title:hover) {
    background-color: #004cae !important;
}
:global(.ant-menu-sub) {
    background-color: #003D8F !important;
}
:global(.ant-menu-submenu .ant-menu-item) {
    background-color: #003D8F !important;
    color: white !important;
}
:global(.ant-menu-submenu .ant-menu-item:hover) {
    background-color: #004cae !important;
}
:global(.ant-menu-item-selected) {
    background-color: white !important;
    color: #003D8F !important;
    font-weight: bold;
}
:global(.ant-menu-sub .ant-menu-item-selected) {
    background-color: white !important;
    color: #003D8F !important;
    font-weight: bold;
}
:global(.ant-menu-submenu-selected > .ant-menu-submenu-title) {
    background-color: white !important;
    color: #003D8F !important;
    font-weight: bold;
}

/* Sidebar scrollbar */
.sidebar ::-webkit-scrollbar {
    width: 6px;
}
.sidebar ::-webkit-scrollbar-thumb {
    background-color: #ccc;
    border-radius: 6px;
}
.sidebar ::-webkit-scrollbar-track {
    background: transparent;
}

.logo {
    width: 100%;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    padding: 10px 20px;
    align-items: center;
}

.menu {
    height: 100%;
    display: flex;
    flex-direction: column;
}

/* 6. DASHBOARD CARDS (RESPONSIVE) */
.cardGrid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 16px;
    margin-bottom: 32px;
    width: 100%;
    box-sizing: border-box;
}
.cardWrapper:hover {
    transform: scale(1.02);
    transition: transform 0.2s ease;
}

/* 7. DASHBOARD CHARTS AND TABLES: Prevent overflow on small screens */
.recharts-responsive-container {
    width: 100% !important;
    min-width: 0 !important;
    max-width: 100% !important;
}

/* 8. RESPONSIVE BREAKPOINTS */
@media (max-width: 1200px) {
    .container {
        grid-template-columns: 1fr;
        height: auto;
    }
    .sidebar {
        display: none;
    }
    .content {
        padding: 0 5px;
    }
}

@media (max-width: 800px) {
    .cardGrid {
        grid-template-columns: 1fr;
    }
}

